$bg: rgb(34, 32, 32);

.header{
    height: 10vh;
    background-color: $bg;
    display: flex;
    padding: 0.5rem;
    box-sizing: border-box;
    justify-content:space-between;
    align-items: center;
    >img{
        height: 100%;
    }
    >div{
        // border:1px solid red;
        width: 100%;
        margin-left: 2vmax;
        >a{
            margin: 1vmax;
            font-size: 1.3rem;
            font-weight: 300; 
        }
    }
    svg{
        color: white;
        font-size: 2rem;
        cursor: pointer;

        & :hover{
            color: rgb(69, 68, 67);
        }
    }
}