$bg: rgb(34, 32, 32);



.banner{
    height: 78vh;
    background-color: $bg;
    box-sizing: border-box;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding: 3rem;
    box-sizing: border-box;

    &::after{
       content: "";
       width: 100%;
       height: 100%;
       position: absolute;
       top: 0%;
       left: 0%;
       background-color: $bg;
       opacity: 0.4;
    }
    h1,p{
        font-size: 4rem;
        z-index: 10;
        width: 50%;
        color: white;

    }
    p{
        font-size: 1.2rem;
        letter-spacing: 2px;
        word-spacing: 2px;
        font-weight: 100;
    }
    >div{
        display: flex;
        button{
            color: black;
            width: 150px;
            padding: 1rem;
            background-color: white;
            border: none;
            outline: none;
            border-radius: 5px;
            z-index: 10;
            margin: 1rem;
            font-size: 1.2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            & :hover{
                opacity: 0.6;
            }

            &:nth-child(odd){
                background-color: white;
                color: black;
            }
            &:nth-child(even){
                background-color: rgb(71, 68, 68);
                color: white;
            }
            >svg{
                font-size: 1.7rem;
                margin: 0 10px;
            }
        }
    }
    
    
}
.row{
    padding: 1rem;
    background-color: $bg;
    >h2{
        font-weight: 400;
        margin: 0.3rem;
    }
    >div{
        display: flex;
        overflow-x: auto;
    }
}

.card{
       width: 150px;
       margin: 0.3rem;
        cursor: pointer;
        transform: translate 0.5s;
        &:hover{
            transform: translateY(-10px) scale(1.1);
        }
   
}

.genreBox{
    background-color: $bg;
    height: 100px;
    overflow-x: auto;
    display: flex;
    
    >a{
        color: black;
        background-color: white;
        padding: 1rem;
         margin: 1rem;
         text-align: center;
        border-radius: 5px;
        &:hover{
            background-color: rgb(115, 95, 95);
        }

    }
}
